export { ChessBoard, validateMove }

class ChessBoard {
    constructor(board = chessBoard.slice()) {
        this.board = board
        this.movePiece = this.movePiece.bind(this)
    }

    movePiece(from,to) {
        if(!this.board[from[0]][from[1]].name ||
           this.board[to[0]][to[1]].name) {
            return false
        }
        const name = this.board[from[0]][from[1]].name
        const color = this.board[from[0]][from[1]].color
        this.clearSquare(from)
        this.putPiece(name,color,to)
        return true
    }

    putPiece(piece,color,square) {
        if(this.board[square[0]][square[1]].name) {
            return false
        }
        let newBoard = JSON.parse(JSON.stringify(this.board))
        newBoard[square[0]][square[1]] = {
            name: piece,
            color: color
        }
        this.board = newBoard
        return true
    }

    clearSquare(square) {
        if(!this.board[square[0]][square[1]].name) {
            return false
        }
        let newBoard = JSON.parse(JSON.stringify(this.board))
        newBoard[square[0]][square[1]] = {}
        this.board = newBoard
        return true
    }
}

