import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import { Peer } from "peerjs";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { solid } from '@fortawesome/fontawesome-svg-core/import.macro'
import { ChessBoard, validateMove } from './chess.js'

class ChessSquare extends React.Component {
  constructor(props) {
    super(props)
    this.onClick = this.onClick.bind(this)
  }

  onClick(e) {
    this.props.onClick(this.props.i,this.props.j)
  }
  
  renderPiece(name) {
    const m = {
      'K': <FontAwesomeIcon icon={solid('chess-king')} />,
      'Q': <FontAwesomeIcon icon={solid('chess-queen')} />,
      'R': <FontAwesomeIcon icon={solid('chess-rook')} />,
      'B': <FontAwesomeIcon icon={solid('chess-bishop')} />,
      'N': <FontAwesomeIcon icon={solid('chess-knight')} />,
      'P': <FontAwesomeIcon icon={solid('chess-pawn')} />,
    }
    return m[name]
  }

  allStyle() {
    return "h-8 w-8 text-center" //"chess-square"
  }
  
  pieceStyle() {
    const white = this.props.piece.color == 'white'
    return white ? 'text-white' : 'text-slate-800'
  }

  squareStyle() {
    const dark = (this.props.i % 2) == (this.props.j % 2)
    if(this.props.mark) {
      return 'bg-red-300'
    } else if(dark) {
      return 'bg-slate-400'
    } else {
      return 'bg-slate-300'
    }
  }
  
  render() {
    const classes = [this.allStyle(),
                     this.pieceStyle(),
                     this.squareStyle()].join(' ')
    console.log(classes)

    return (
      <button className={classes} onClick={this.onClick}>
        {this.renderPiece(this.props.piece.name)}
      </button>
    );
  }
}

class ChessBoardView extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const isMarked = (i,j) => {
      const indexOf = this.props.marks.findIndex((s) => {
        return s[0] == i && s[1] == j
      })
      return indexOf > -1
    }
    const rows = [];
    this.props.board.forEach((row,i) => {
      const squares = []
      row.forEach((square,j) => {
        squares.unshift(<ChessSquare key={-(j+1)}
                                     i={i}
                                     j={j}
                                     mark={isMarked(i,j)}
                                     piece={square}
                                     onClick={this.props.onClick} /> );
      });
      rows.push(<div key={i} className="flex flex-row w-full">{squares}</div>)
    });
    return <div className="flex flex-col flex-wrap justify-center text-center">{rows}</div>;
  }
}

class ConnectionPanel extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fieldValue: ""
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmitJoin = this.handleSubmitJoin.bind(this);
    this.handleSubmitNew = this.handleSubmitNew.bind(this);
    this.handleKey = this.handleKey.bind(this);
  }

  handleSubmitNew(event) {
    this.props.host();
  }
  
  handleChange(event) {
    this.setState({fieldValue: event.target.value});
  }

  handleSubmitJoin(event) {
    event.preventDefault(); // prevents page refresh
    this.props.join(this.state.fieldValue);
  }

  handleKey(event) {
    if(event.key === 'Enter') {
      this.props.join(this.state.fieldValue);
    }
  }
  
  render() {
    return (
      <div className="flex flex-col gap-2 w-96">
        <div className="flex-1">
          <button className="p-4 bg-slate-400 w-96"
                  onClick={this.handleSubmitNew}>New chess board</button>
        </div>
        <div className="flex-1">
          <button className="p-4 bg-slate-400 w-96"
                  onClick={this.handleSubmitJoin}>Join chess board</button>
          <input className="border w-96 p-2 text-center"
                 value={this.state.fieldValue}
                 onChange={this.handleChange}
                 onKeyPress={this.handleKey}/>
        </div>
      </div>
    );
  }
}

class UI extends React.Component {
  constructor(props) {
    super(props);
    // see https://reactjs.org/docs/forms.html
    this.connectToID = this.connectToID.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.receiveConnection = this.receiveConnection.bind(this);
    this.initializePeer = this.initializePeer.bind(this);
    this.hostBoard = this.hostBoard.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleKey = this.handleKey.bind(this);
    this.state = {
      peer: null,
      myID: null,
      friendID: null,
      conn: null,
      show: false,
      host: false,
      board: new ChessBoard(),
      fieldValue: "",
      src: null,
      isWhite: null,
      isWhiteNext: true
    };
  }

  componentDidMount() {
    this.initializePeer()
  }

  initializePeer() {
    const p = new Peer();
    p.on('open', (id) => {
      this.setState({
        myID: id,
      });
    });
    p.on('connection', this.receiveConnection);
    this.setState({
      peer: p
    });
  }

  hostBoard() {
    this.setState({
      show: true,
      host: true,
      isWhite: true
    })
  }

  handleClick(i,j) {
    if(!this.myTurn()) {
      return false
    }
    if(!this.state.src) {
      this.setState({
        src: [i,j]
      })
    } else {
      this.doMove(this.state.src,[i,j])
      this.setState({
        src: null,
      })
      this.state.conn.send({
        from: this.state.src,
        to: [i,j]
      })
    }
  }

  myTurn() {
    return this.state.isWhite == this.state.isWhiteNext
  }

  handleData(d) {
    console.log("received: ",d)
    if(this.myTurn()) {
      // if we get here, something's gone wrong
      console.log("opponent moved received even though it's our turn")
      return
    }
    this.doMove(d.from,d.to);
  }

  doMove(from,to) {
    this.setState({
      isWhiteNext: !this.state.isWhiteNext
    })
    this.state.board.clearSquare(to)
    this.state.board.movePiece(from,to)
  }

  receiveConnection(c) {
    console.log("received connection")
    c.on('data', (data) => {
      this.handleData(data)
    });
    this.setState({
      isX: true,
      conn: c,
    })
  }
  
  connectToID(id) {
    const c = this.state.peer.connect(id);
    c.on("error", (err) => {
      console.log(err)
    });
    c.on("open", () => {
      console.log("sent connection successful")
    });
    c.on('data', (data) => {
      this.handleData(data)
    });
    this.setState({
      show: true,
      isX: false,
      conn: c,
      friendID: id,
      isWhite: false
    })
  }
  
  handleChange(event) {
    this.setState({fieldValue: event.target.value});
  }

  doMoveAN() {
    /*
    const i1 = parseInt(an.charAt(0),10)
    const j1 = parseInt(an.charAt(1),10)
    const i2 = parseInt(an.charAt(2),10)
    const j2 = parseInt(an.charAt(3),10)
    const newBoard = execMove([i1,j1],[i2,j2],this.state.board)
    console.log(newBoard)
    this.setState({
      board: newBoard
    })
    */
    //    console.log(validateMove(an,'white',this.state.board))
  }
  
  handleKey(event) {
    if(!this.myTurn()) {
      return false
    }
    if(event.key === 'Enter') {
      this.doMoveAN(this.state.fieldValue);
      this.setState({
        fieldValue: ""
      })
    }
  }

  gameOver() {
    return false
  }
  
  render() {
    const winner = false //calculateWinner(this.state.squares);
    let status;
    if (!this.state.show) {
      status = null
    } else if (this.state.conn == null) {
      status = 'Waiting for an opponent to join'
    } else if (winner) {
      status = 'You ' + ((winner == 'X') == this.state.isX ? 'won!' : 'lost!')
    } else if (this.gameOver()) {
      status = 'Game over: no winner!'
    } else {
      status = this.myTurn() ? 'Your move' : 'Waiting for opponent to play...';
    }
    const myTurn = this.myTurn()
    const id = this.state.host ? "Board ID: ".concat(this.state.myID) : null
    const marks = this.state.src ? [this.state.src] : []
    let board = null
    if(this.state.show) {
      board = <ChessBoardView board={this.state.board.board}
                              onClick={this.handleClick}
                              marks={marks} />
    }
    let anInput
    if(false) {
      anInput = <input className="border w-96 p-2 text-center"
               value={this.state.fieldValue}
               onChange={this.handleChange}
               onKeyPress={this.handleKey}/>
    }
    
    return (
      <div className="flex flex-col items-center basis-96">
        <div className={'pb-10'}>
          <ConnectionPanel host={this.hostBoard}
                           join={this.connectToID}/>
        </div>
        <div className={'pb-10'}>
          <p>{id}</p>
          <p>{status}</p>
        </div>
        <div className="">{board}</div>
        {anInput}
      </div>
    );
  }
}

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(<UI />);